import React, { PureComponent } from 'react';
import classNames from 'classNames';
import { isFunction } from '../typeof';
export default class TouchFeedback extends PureComponent {
    constructor() {
        super(...arguments);
        this.state = {
            active: false
        };
        this.onTouchStart = (e) => {
            this.triggerEvent('TouchStart', true, e);
        };
        this.onTouchMove = (e) => {
            this.triggerEvent('TouchMove', false, e);
        };
        this.onTouchEnd = (e) => {
            this.triggerEvent('TouchEnd', false, e);
        };
        this.onTouchCancel = (e) => {
            this.triggerEvent('TouchCancel', false, e);
        };
        this.onMouseDown = (e) => {
            // pcTouchStart
            if (this.props.onTouchStart) {
                this.triggerEvent('TouchStart', true, e);
            }
            this.triggerEvent('MouseDown', true, e);
        };
        this.onMouseUp = (e) => {
            // pcTouchEnd
            if (this.props.onTouchEnd) {
                this.triggerEvent('TouchEnd', true, e);
            }
            this.triggerEvent('MouseUp', false, e);
        };
        // mtouch
        this.onMouseLeave = (e) => {
            this.triggerEvent('MouseLeave', false, e);
        };
    }
    triggerEvent(type, isActive, e) {
        // 
        // 
        const eventType = `on${type}`;
        if (this.props[eventType]) {
            isFunction(this.props[eventType]) && this.props[eventType](e);
        }
        if (isActive !== this.state.active) {
            this.setState({
                active: isActive
            });
        }
    }
    render() {
        const { children, activeStyle, activeClassName, disabled } = this.props;
        const events = {
            onTouchStart: this.onTouchStart,
            onTouchMove: this.onTouchMove,
            onTouchEnd: this.onTouchEnd,
            onTouchCancel: this.onTouchCancel,
            onMouseDown: this.onMouseDown,
            onMouseUp: this.onMouseUp,
            onMouseLeave: this.onMouseLeave
        };
        const child = React.Children.only(children);
        if (!disabled && this.state.active) {
            let { style, className } = child.props;
            // style
            if (activeStyle) {
                style = Object.assign({}, style, activeStyle);
            }
            // classname
            className = classNames(className, activeClassName);
            // touchclassname
            return React.cloneElement(child, Object.assign({ className,
                style }, events));
        }
        // 
        return React.cloneElement(child, events);
    }
}
TouchFeedback.defaultProps = {
    activeStyle: null,
    activeClassName: '',
    disabled: false
};



